@import "../../constants.scss";

.qr-code-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 24px;
  padding: 16px;
  svg {
    display: block;
    width: 200px;
    height: auto;
    aspect-ratio: 1 / 1;
    padding: 8px;
    background: #ffffff;
    border-radius: 8px;
  }
  .wallet-address {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 1fr;
    align-items: center;
    justify-content: space-between;
    gap: 8px;
    font-size: 14px;
    font-weight: 500;
    width: 100%;
    .title {
      color: $textSecondaryColor;
      text-align: left;
    }
    .address {
      text-align: right;
      font-size: 14px;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }
}
